import { FunctionComponent } from "react";
import styles from "./Locations.module.css";

type LocationsType = {
  depCode?: string;
  depName?: string;
  flightIcon?: string;
  arrCode?: string;
  arrName?: string;
};

const Locations: FunctionComponent<LocationsType> = ({
  depCode,
  depName,
  flightIcon,
  arrCode,
  arrName,
}) => {
  return (
    <div className={styles.locations}>
      <div className={styles.departure}>
        <div className={styles.depcode}>{depCode}</div>
        <div className={styles.depname}>{depName}</div>
      </div>
      <img className={styles.flightIcon} alt="" src={flightIcon} />
      <div className={styles.arrival}>
        <div className={styles.depcode}>{arrCode}</div>
        <div className={styles.arrname}>{arrName}</div>
      </div>
    </div>
  );
};

export default Locations;
